* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #2D2D2D;
  outline-offset: 4px;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
  outline: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

.wrapper {
  width: 100%;
  min-height: auto;
  overflow: hidden;
}

.main__home {
  min-height: 757px;
  height: 1px;
  padding-top: 155px;
  padding-bottom: 20px;
}
@media (max-width: 480px) {
  .main__home {
    min-height: 630px;
    padding-top: 100px;
  }
}

.container {
  max-width: 1171px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 765px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #48B774;
  color: #ffffff;
  border-radius: 20px;
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: #37905a;
  }
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../Poppins-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../Poppins-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.header__body {
  position: relative;
  z-index: 100;
  min-height: 102px;
  display: flex;
  align-items: center;
  padding-right: 28px;
}
@media (max-width: 1200px) {
  .header__body {
    padding-right: 0;
  }
}
@media (max-width: 480px) {
  .header__body {
    min-height: 70px;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  font-size: 25px;
  line-height: 38px;
  color: #2D2D2D;
  margin-right: 23.4%;
}
.header__logo svg {
  margin-right: 8px;
}
@media (max-width: 480px) {
  .header__logo {
    margin-right: 10px;
  }
}
@media (min-width: 992px) {
  .header__menu {
    flex: 0 0 400px;
    padding-right: 20px;
  }
}
@media (max-width: 992px) {
  .header__menu {
    display: none;
    position: absolute;
    top: 80px;
    right: 0;
    z-index: 50;
    background-color: #ffffff;
    padding: 15px;
    border-radius: 5px;
    background-color: #48B774;
  }
}
@media (max-width: 480px) {
  .header__menu {
    top: 60px;
  }
}
.header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 992px) {
  .header__list {
    flex-direction: column;
  }
}
.header__item:not(:last-child) {
  margin-right: 44px;
}
@media (max-width: 992px) {
  .header__item:not(:last-child) {
    margin: 0 0 10px 0;
  }
}
.header__link {
  position: relative;
  font-weight: 400;
  line-height: 24px;
  color: #4A4A4A;
}
.header__link::before {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  width: 21px;
  height: 1.5px;
  background-color: #FB9B69;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
@media (any-hover: hover) {
  .header__link:hover::before {
    opacity: 1;
    visibility: visible;
  }
}
@media (max-width: 992px) {
  .header__link {
    color: #ffffff;
  }
}
.header__btn {
  height: 42px;
  padding: 9px 30px;
  line-height: 24px;
  margin-left: auto;
}
@media (max-width: 992px) {
  .header__btn {
    margin-right: 15px;
  }
}
@media (max-width: 480px) {
  .header__btn {
    height: 35px;
    padding: 5px 22px;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 992px) {
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .header__burger span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #4a4a4a;
  }
  .header__burger span:first-child {
    top: 0;
  }
  .header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .header__burger.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 992px) and (any-hover: hover) {
  .header__burger:hover span {
    background-color: #d66e3a;
  }
}

.home {
  background-color: #ECF7F3;
}
.home__container {
  height: 100%;
}
.home__body {
  display: flex;
  height: 100%;
}
@media (max-width: 765px) {
  .home__body {
    display: block;
  }
}
.home__info {
  padding-top: 40px;
  margin-right: 75px;
}
@media (max-width: 765px) {
  .home__info {
    margin-right: 0;
    padding-top: 0;
  }
}
.home__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 63px;
  color: #2D2D2D;
  margin-bottom: 22px;
}
.home__title span {
  color: #FF8345;
}
@media (max-width: 992px) {
  .home__title {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 15px;
  }
}
.home__descr {
  font-size: 15px;
  line-height: 26px;
  color: #7C7C7C;
  margin-bottom: 30px;
}
@media (max-width: 992px) {
  .home__descr {
    margin-bottom: 20px;
  }
}
@media (max-width: 765px) {
  .home__descr {
    max-width: 500px;
  }
}
@media (max-width: 480px) {
  .home__descr {
    line-height: 23px;
  }
}
.home__btn {
  height: 46px;
  padding: 10px 30px;
  margin-bottom: 50px;
}
@media (max-width: 992px) {
  .home__btn {
    margin-bottom: 35px;
  }
}
@media (max-width: 480px) {
  .home__btn {
    margin-bottom: 20px;
  }
}
.home__search {
  width: 597px;
  height: 62px;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 0px 20px 30px rgba(222, 222, 222, 0.25);
  border-radius: 30px;
  padding: 4px 6px 4px 20px;
  margin-bottom: 40px;
}
@media (max-width: 992px) {
  .home__search {
    width: 400px;
    margin-bottom: 30px;
  }
}
@media (max-width: 480px) {
  .home__search {
    width: 100%;
    height: 50px;
    margin-bottom: 30px;
  }
}
.home__users {
  display: flex;
  align-items: center;
}
.home__animation {
  position: relative;
}
@media (max-width: 765px) {
  .home__animation {
    display: none;
  }
}

.home-search__label {
  display: block;
  width: 100%;
  height: 100%;
}
.home-search__input {
  width: 100%;
  height: 100%;
  padding: 10px;
  outline: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  color: #A2A2A2;
}
.home-search__input::placeholder {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.01em;
  color: #A2A2A2;
}
.home-search__btn {
  flex: 0 0 42px;
  height: 42px;
  border-radius: 100%;
}

.home-users__icons {
  position: relative;
  margin-right: 12px;
  flex: 0 0 101px;
  height: 38px;
}
.home-users__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 38px;
  height: 100%;
  z-index: 4;
}
.home-users__icon img {
  width: 100%;
  height: 100%;
}
.home-users__icon:nth-child(2) {
  left: 21px;
  z-index: 3;
}
.home-users__icon:nth-child(3) {
  left: 42px;
  z-index: 2;
}
.home-users__icon:nth-child(4) {
  left: 63px;
  z-index: 1;
}
.home-users__descr {
  font-family: "Roboto";
  font-weight: 400;
  line-height: 19px;
  color: #707070;
}
.home-users__link {
  color: #FD905A;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .home-users__link:hover {
    color: #d66e3a;
  }
}

.home-animation__ring {
  position: relative;
  width: 450px;
  height: 450px;
  border: 2px solid #E8E8E8;
  border-radius: 100%;
}
.home-animation__flag {
  position: absolute;
  z-index: 1;
  border-radius: 100%;
}
.home-animation__flag img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}
.home-animation__flag:nth-child(1) {
  width: 50px;
  height: 50px;
  top: 247px;
  left: -20px;
}
.home-animation__flag:nth-child(2) {
  width: 80px;
  height: 80px;
  top: 77px;
  left: -10px;
}
.home-animation__flag:nth-child(3) {
  width: 60px;
  height: 60px;
  top: -30px;
  left: 225px;
}
.home-animation__flag:nth-child(4) {
  width: 70px;
  height: 70px;
  top: 87px;
  left: 390px;
}
.home-animation__flag:nth-child(5) {
  width: 50px;
  height: 50px;
  top: 247px;
  left: 417px;
}
.home-animation__circle {
  position: absolute;
  top: 117px;
  left: 90px;
  z-index: 2;
  width: 280px;
  height: 280px;
  border-radius: 100%;
  background-color: #48B774;
}
.home-animation__image {
  position: absolute;
  bottom: 0;
  right: 72px;
  width: 298px;
  height: 557px;
  z-index: 3;
}
.home-animation__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}